import React, { useEffect } from "react";
import "../App.css";
import Note from "./note";
import { useState } from "react";
export default function HomePage(props) {
  const [notes, setNotes] = useState([]);

  useEffect(() => {
    getNotes();
  }, []);

  const getNotes = async () => {
    //Get token
    const token = localStorage.getItem("token");
    //if token is valid
    if (token) {
      //setup up fetch
      const myHeaders = new Headers();
      myHeaders.append("Authorization", "Bearer " + token);
      myHeaders.append("Content-Type", "application/json");
      const requestOptions = {
        method: "GET",
        headers: myHeaders,
      };

      const response = await fetch(
        "http://localhost:5000/notes",
        requestOptions
      );
      try {
        if (response.status === 403) {
          localStorage.removeItem("token");
        } else {
          const result = await response.json();
          console.log(result);
          setNotes(result.result);
        }
      } catch (error) {
        console.log(error);
      }
    }
  };

  return (
    <div>
      <header>
        <span className="Title">Cloud Notes</span>
      </header>

      <div className="container" style={{ paddingTop: "5vh" }}>
        {this.state.notes.map((note) => {
          const randomColor = () => Math.ceil(Math.random() * 255);
          const r = randomColor();
          const g = randomColor();
          const b = randomColor();
          return (
            <svg
            >
              <g
                transform="matrix(0.83 0 0 0.83 325.24 330.38)"
                id="oTTTnywJL-DJuk739jFXE"
              >
                <path
                  style={`stroke: rgb(${r},${g},${b}); stroke-width: 0; stroke-dasharray: none; stroke-linecap: butt; stroke-dashoffset: 0; stroke-linejoin: miter; stroke-miterlimit: 4; fill: rgb(190,191,248); fill-rule: nonzero; opacity: 1;`}
                  vector-effect="non-scaling-stroke"
                  transform=" translate(0, 0)"
                  d="M -87.31618 -50.27107 C -87.31618 -70.73052 -70.73052 -87.31619 -50.271060000000006 -87.31619 L 50.271069999999995 -87.31619 L 50.271069999999995 -87.31619 C 70.73052 -87.31619 87.31618999999999 -70.73053 87.31618999999999 -50.27107000000001 L 87.31618999999999 50.27105999999999 L 87.31618999999999 50.27105999999999 C 87.31618999999999 70.73051 70.73052999999999 87.31617999999999 50.271069999999995 87.31617999999999 L -50.271060000000006 87.31617999999999 L -50.271060000000006 87.31617999999999 C -70.73051000000001 87.31617999999999 -87.31618 70.73051999999998 -87.31618 50.27105999999999 z"
                  stroke-linecap="round"
                />
              </g>
              <g
                transform="matrix(0.36 0 0 0.36 325.04 304.63)"
                id="p-YEpVBOlDpcS4hhwHnMB"
              >
                <path
                  style="stroke: rgb(0,0,0); stroke-width: 1; stroke-dasharray: none; stroke-linecap: butt; stroke-dashoffset: 0; stroke-linejoin: miter; stroke-miterlimit: 4; fill: rgb(255,255,255); fill-opacity: 0; fill-rule: nonzero; opacity: 1;"
                  vector-effect="non-scaling-stroke"
                  transform=" translate(0, 0)"
                  d="M -55.02831 0 C 78.72169 0 91.22169 0 91.22169 0 C 91.22169 0 -188.77831 0 -55.028310000000005 0 z"
                  stroke-linecap="round"
                />
              </g>
              <g
                transform="matrix(0.36 0 0 0.36 325.24 354.63)"
                id="orw-GXQvJoboAWYimHJiC"
              >
                <path
                  style="stroke: rgb(0,0,0); stroke-width: 1; stroke-dasharray: none; stroke-linecap: butt; stroke-dashoffset: 0; stroke-linejoin: miter; stroke-miterlimit: 4; fill: rgb(255,255,255); fill-opacity: 0; fill-rule: nonzero; opacity: 1;"
                  vector-effect="non-scaling-stroke"
                  transform=" translate(0, 0)"
                  d="M -55.02831 0 C 78.72169 0 91.22169 0 91.22169 0 C 91.22169 0 -188.77831 0 -55.028310000000005 0 z"
                  stroke-linecap="round"
                />
              </g>
              <g
                transform="matrix(0.36 0 0 0.36 325.24 330.88)"
                id="9D7nKIW-8lTYOMZf41xWq"
              >
                <path
                  style="stroke: rgb(0,0,0); stroke-width: 1; stroke-dasharray: none; stroke-linecap: butt; stroke-dashoffset: 0; stroke-linejoin: miter; stroke-miterlimit: 4; fill: rgb(255,255,255); fill-opacity: 0; fill-rule: nonzero; opacity: 1;"
                  vector-effect="non-scaling-stroke"
                  transform=" translate(0, 0)"
                  d="M -55.02831 0 C 78.72169 0 91.22169 0 91.22169 0 C 91.22169 0 -188.77831 0 -55.028310000000005 0 z"
                  stroke-linecap="round"
                />
              </g>
            </svg>
          );
        })}
      </div>
    </div>
  );
}
